.abbreviation
{
    background-color: #e9ecef;
    padding: 20px;
    border-radius: 5px;
    margin-top: 100px;

    display: grid;
    grid-template-columns: repeat(5, 1fr);
    grid-template-rows: repeat(2, 3em);
    column-gap: 5px;
    row-gap: 5px;
}

.abbreviation-item {
    display: inline-block;
    border-radius: 10px;
    min-width: 2em;

    text-align: center;
    text-underline-offset: 2px;
    font-weight: 500;
    
    padding: 5px;
    margin: 5px 5px;
}